@-webkit-keyframes action {
from {
			-webkit-transform: rotate(0deg)  translateX(0px) rotate(0deg);
			        transform: rotate(0deg)  translateX(0px) rotate(0deg);
}
to {
			-webkit-transform: rotate(360deg) translateX(0px) rotate(0deg);
			        transform: rotate(360deg) translateX(0px) rotate(0deg);
}
}
@keyframes action {
from {
			-webkit-transform: rotate(0deg)  translateX(0px) rotate(0deg);
			        transform: rotate(0deg)  translateX(0px) rotate(0deg);
}
to {
			-webkit-transform: rotate(360deg) translateX(0px) rotate(0deg);
			        transform: rotate(360deg) translateX(0px) rotate(0deg);
}
}
@-webkit-keyframes spin {
to { -webkit-transform: rotate(1turn); transform: rotate(1turn);
}
}
@keyframes spin {
to { -webkit-transform: rotate(1turn); transform: rotate(1turn);
}
}
@-webkit-keyframes xuanzhuan {
from {
			-webkit-transform:rotate(0deg);
			        transform:rotate(0deg);
}
to {
			-webkit-transform:rotate(360deg);
			        transform:rotate(360deg);
}
}
@keyframes xuanzhuan {
from {
			-webkit-transform:rotate(0deg);
			        transform:rotate(0deg);
}
to {
			-webkit-transform:rotate(360deg);
			        transform:rotate(360deg);
}
}
@-webkit-keyframes xuanzhuanfan {
from {
			-webkit-transform:rotate(0deg);
			        transform:rotate(0deg);
}
to {
			-webkit-transform:rotate(-360deg);
			        transform:rotate(-360deg);
}
}
@keyframes xuanzhuanfan {
from {
			-webkit-transform:rotate(0deg);
			        transform:rotate(0deg);
}
to {
			-webkit-transform:rotate(-360deg);
			        transform:rotate(-360deg);
}
}
@-webkit-keyframes xuanzhuanfan1 {
from {
			-webkit-transform:rotate(360deg);
			        transform:rotate(360deg);
}
to {
			-webkit-transform:rotate(0deg);
			        transform:rotate(0deg);
}
}
@keyframes xuanzhuanfan1 {
from {
			-webkit-transform:rotate(360deg);
			        transform:rotate(360deg);
}
to {
			-webkit-transform:rotate(0deg);
			        transform:rotate(0deg);
}
}
