.show {
  font-family: '\6977\4F53';
  width: 100%;
  height: 100%;
  color: white;
  font-size: 50px;
  text-align: center;
  /*background: rgba(255,255,255,0.2);*/
  position: relative;
}
.show-wrap {
  width: 1160px;
  height: 70%;
  /*background: rgba(255, 255, 255, 0.5);*/
  margin: 20px auto;
}
.show-demo {
  width: 210px;
  height: 220px;
  background: rgba(255,255,255,0.6);
  border-radius: 5px;
  position: relative;
  float: left;
  margin-right: 20px;
  margin-top: 60px;
  /* margin-left: 30px; */
  overflow: hidden;
  -webkit-transition: all .3s;
  transition: all .3s;
}
.show-demo:hover {
  background: rgba(255,255,255,0.9);
}
.show-demo:hover .demos {
  opacity: 1;
}
.show-demo:hover .demo-top {
	/*color:white;*/
}
.show-demo:hover .ico-span {
  -webkit-transform: translate(0,83px);
          transform: translate(0,83px);
}
.show-demo:hover .ico-line {
  -webkit-transform: translate(0,0);
          transform: translate(0,0);
}
.demos {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  cursor: pointer;
  background: rgba(0, 0, 0, 0.8);
  -webkit-transition: all .3s;
  transition: all .3s;
  opacity: 0;
}
.ico-span {
  position: absolute;
  left: 50%;
  /* top: -20px; */
  margin-left: -27px;
  /* margin-top: -31px; */
  -webkit-transform: translate(0,-50px);
          transform: translate(0,-50px);
  padding: 5px;
  display: inline-block;
  border: 2px solid white;
  border-radius: 50%;
  -webkit-transition: all .3s;
  transition: all .3s;
}
.ico-span>a {
  position: absolute;
  top:0;
  left:0;
  right: 0;
  bottom:0;
}
.ico-span:hover {
  background: rgba(255, 255, 255, 0.5);
}
.ico-line {
  position: absolute;
  display: block;
  height: 84px;
  width: 2px;
  background: white;
  left: 50%;
  margin-left: -2px;
  border-radius: 2px;
  /* top: -134px; */
  -webkit-transform: translate(0,-134px);
          transform: translate(0,-134px);
  -webkit-transition: all .3s;
  transition: all .3s;
}
.icono-home {
  margin-bottom: 5px;
  margin-left: 8px;
  margin-right: 8px;
}
.show-demo .demo-type {
  font-size: 18px;
  line-height: 38px;
  padding-left: 6px;
}
